.mypara{
    background-color:yellow;
    height: 50px;
    text-align: left;
    padding: 10px;
    border: 2px solid black;
    border-radius: 3px; 
}
.my-nav{
    background-color: #6610F2;
    color: #fff;
}
.navbar-brand{
    font-size: 1.5rem;
    font-weight: bold;
    color: #fff;
}
.navbar-brand:hover{
    color: #fff;
    cursor: pointer;
}
.nav-link1{
    color: #b18cec;
    font-size: 1rem;
    font-weight: 500;
    align-items: baseline;
}
.nav-link2 {
    color: #cac4d4;
    font-size: 1rem;
    font-weight: 500;
    align-items: baseline;
}


.nav-link1:hover , .nav-link2:hover  {
    
    color: #fff;
}

.my-btn{
    border: 1px solid #fff;
    color: #b18cec;
    background: #6610F2;
}
.my-btns{
    background: #fff;
}
.my-btn:hover{
    border: 1px solid #fff;
    color: #fff;
    background: #6610f1;;
}